export default class TokenType {
  //  
  name: string; //  
  regex: string; //  

  constructor(name: string, regex: string) {
    //
    this.name = name;
    this.regex = regex;
  }
}

export const tokenTypesList = {
  NUMBER: new TokenType("NUMBER", "[0-9]*"),
  TYPE: new TokenType(
    "TYPE",
    "(_|_|__|__|_)"
  ),
  VARIABLE: new TokenType("VARIABLE", "[-]*"),
  SEMICOLON: new TokenType("SEMICOLON", "\\n"),
  SPACE: new TokenType("SPACE", "[ \\t\\r]"),
  ASSIGN: new TokenType("ASSIGN", "\\="),
  INPUT: new TokenType("INPUT", "_"),
  HEADER: new TokenType("HEADER", ""),
  QUOTES: new TokenType("QUOTES", `[\\"\\']`),
  BRACKETS_LEFT: new TokenType("BRACKETS_LEFT", `\\(`),
  BRACKETS_RIGHT: new TokenType("BRACKETS_RIGHT", `\\)`),
  COMMA: new TokenType("COMMA", `\\,`),
  DESCRIPTION: new TokenType("DESCRIPTION", ""),
  SUBMIT: new TokenType("SUBMIT", ""),
  START: new TokenType("START", "_"),
  FINISH: new TokenType("FINISH", "_"),
};
